.main {
    margin-top: 4em;
    background-color: rgb(232, 254, 255);
    box-shadow:  rgba(0, 222, 89, 0.1) 0px 0px 0px 1px;  
  }
  
  .tools {
    width: 100%;
    text-align: center;
    background-color: var(--clr-bg);
    height: 35vh;
    background-color: rgb(232, 254, 255);
    
  }
  
  .tools > div {
    width: 11%;
    height: 16vh;
    max-height: 140px;
    box-shadow: var(--shadow);
    z-index: 1;
    border-radius: 20px;
    margin: 2.5%;
    float: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease 0s;
  }
  .tools > div:hover {
    background-color: var(--clr-bg);
    overflow: hidden;
    transform: scale(1.08);
    box-shadow: rgba(0, 0, 0, 0.36) 0px 10px 36px 0px,
      rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    border: 1px solid var(--clr-secondary);
  }
  
  .tools > div > svg {
    font-size: 8vh;
    color: var(--clr-secondary);
  }
  .tools > div > h5 {
    position: relative;
    top: 8px;
  }
  
  @media (max-width: 600px) {
    .tools {
      height: 90vh;
    }
    .tools > div {
      width: 40%;
      margin-right: 5%;
      margin-bottom: 2%;
      height: 18vh;
    }
  }
  